// ============================================
// GLOBAL CENTRALIZED CACHE FOR INSTANT RESPONSE
// ============================================
// This cache is shared across ALL components for instant data access

interface CacheEntry<T> {
  data: T;
  timestamp: number;
}

const CACHE_TTL = 24 * 60 * 60 * 1000; // 24 hour cache lifetime (extended for demo)

class GlobalDataCache {
  private cache: Map<string, CacheEntry<any>> = new Map();
  private pendingRequests: Map<string, Promise<any>> = new Map();
  
  // Track preload progress
  public preloadProgress = {
    total: 0,
    completed: 0,
    inProgress: false,
    currentDrug: '',
  };
  
  // Listeners for cache updates
  private listeners: Set<() => void> = new Set();
  
  get<T>(key: string): T | null {
    const entry = this.cache.get(key);
    if (!entry) return null;
    
    // Check if cache is still valid
    if (Date.now() - entry.timestamp > CACHE_TTL) {
      this.cache.delete(key);
      return null;
    }
    
    return entry.data as T;
  }
  
  set<T>(key: string, data: T): void {
    this.cache.set(key, {
      data,
      timestamp: Date.now()
    });
    this.notifyListeners();
  }
  
  has(key: string): boolean {
    return this.get(key) !== null;
  }
  
  clear(): void {
    this.cache.clear();
    this.pendingRequests.clear();
    this.preloadProgress = { total: 0, completed: 0, inProgress: false, currentDrug: '' };
    this.notifyListeners();
  }
  
  // Deduplicate concurrent requests for same key
  async getOrFetch<T>(key: string, fetcher: () => Promise<T>): Promise<T> {
    // Return from cache if available
    const cached = this.get<T>(key);
    if (cached !== null) {
      return cached;
    }
    
    // If request already in flight, wait for it
    if (this.pendingRequests.has(key)) {
      return this.pendingRequests.get(key)!;
    }
    
    // Create new request
    const promise = fetcher().then((data) => {
      this.set(key, data);
      this.pendingRequests.delete(key);
      return data;
    }).catch((error) => {
      this.pendingRequests.delete(key);
      throw error;
    });
    
    this.pendingRequests.set(key, promise);
    return promise;
  }
  
  // Get cache statistics
  getStats() {
    return {
      size: this.cache.size,
      preloadProgress: this.preloadProgress,
      keys: Array.from(this.cache.keys()),
    };
  }
  
  // Subscribe to cache updates
  subscribe(listener: () => void) {
    this.listeners.add(listener);
    return () => this.listeners.delete(listener);
  }
  
  private notifyListeners() {
    this.listeners.forEach(listener => listener());
  }
}

// Global singleton cache instance - shared across ALL components
const globalCache = new GlobalDataCache();

// Legacy compatibility
const dataCache = globalCache;

