const requestAttentionPair = async (
  diseaseID: string,
  drugID: string
): Promise<{ attention: { [k: string]: IAttentionTree }; metapaths: IPath[] }> => {
  // Check cache first
  const cacheKey = `attention_pair:${drugID}:${diseaseID}`;
  const cached = dataCache.get<{ attention: { [k: string]: IAttentionTree }; metapaths: IPath[] }>(cacheKey);
  if (cached) {
    console.log(`[Cache HIT] attention_pair for ${drugID}-${diseaseID}`);
    return cached;
  }
  
  console.log(`[Cache MISS] fetching attention_pair for ${drugID}-${diseaseID}`);
  const url = `./api/attention_pair?disease=${diseaseID}&drug=${drugID}`;
  let response = await axiosInstance.get(url);
  
  // Store in cache
  dataCache.set(cacheKey, response.data);
  return response.data;
};

