interface SubgraphNode {
  id: string;
  label: string;
  type: string;
}

interface SubgraphEdge {
  source: string;
  target: string;
  relation: string;
  layer1_att: number;
  layer2_att: number;
  attention: number;
}

interface Subgraph {
  nodes: SubgraphNode[];
  edges: SubgraphEdge[];
}

