import * as React from 'react';
import { IState, IDispatch } from 'types';
import { LOADING_ICON, getNodeColor } from 'helpers';
import { requestBestPath } from 'stores/DataService';

interface Props {
  height: number;
  width: number;
  globalState: IState;
  dispatch: IDispatch;
}

interface PathNode {
  nodeId: string;
  nodeType: string;
  nodeName?: string;
}

interface PathEdge {
  edgeInfo: string;
  score: number;
}

interface PathData {
  nodes: PathNode[];
  edges: PathEdge[];
  path_score: number;
}

interface BestPathState {
  loading: boolean;
  error: string | null;
  pathData: {
    drug_id?: string;
    disease_id?: string;
    disease_node_id?: string;
    disease_name?: string;
    paths?: PathData[];
    path?: PathData | null;
  } | null;
}

export default class BestPath extends React.Component<Props, BestPathState> {
  constructor(props: Props) {
    super(props);
    this.state = {
      loading: false,
      error: null,
      pathData: null,
    };
  }

  componentDidMount() {
    this.loadBestPath();
  }

  componentDidUpdate(prevProps: Props) {
    const { selectedDrugPrimary, diseasePredictions } = this.props.globalState;
    const prevDrug = prevProps.globalState.selectedDrugPrimary;
    const prevDiseases = prevProps.globalState.diseasePredictions;

    const selectedDisease = diseasePredictions.find((d) => d.selected);
    const prevSelectedDisease = prevDiseases.find((d) => d.selected);

    if (
      selectedDrugPrimary !== prevDrug ||
      selectedDisease?.id !== prevSelectedDisease?.id
    ) {
      this.loadBestPath();
    }
  }

  async loadBestPath() {
    const { selectedDrugPrimary, diseasePredictions } = this.props.globalState;
    const selectedDisease = diseasePredictions.find((d) => d.selected);

    if (!selectedDrugPrimary || !selectedDisease) {
      this.setState({ pathData: null, loading: false, error: null });
      return;
    }

    const diseaseNodeId = selectedDisease.node_id || selectedDisease.id;

    this.setState({ loading: true, error: null });
    try {
      const res = await requestBestPath(selectedDrugPrimary, diseaseNodeId);
      this.setState({ pathData: res, loading: false });
    } catch (error) {
      console.error('Failed to load best path:', error);
      this.setState({ loading: false, error: 'Failed to load best path' });
    }
  }

  renderSinglePath(path: PathData, rank: number) {
    const { nodeNameDict } = this.props.globalState;
    
    return (
      <div
        key={`path-${rank}`}
        style={{
          marginBottom: 16,
          padding: 12,
          border: '1px solid #e9ecef',
          borderRadius: 8,
          background: rank === 1 ? '#f8fff8' : '#fff',
        }}
      >
        {/* Path header with rank and score */}
        <div
          style={{
            display: 'flex',
            alignItems: 'center',
            marginBottom: 10,
            gap: 12,
          }}
        >
          <div
            style={{
              width: 28,
              height: 28,
              borderRadius: '50%',
              background: rank === 1 ? '#28a745' : rank <= 3 ? '#3a7bd5' : '#6c757d',
              color: 'white',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontWeight: 700,
              fontSize: 14,
            }}
          >
            {rank}
          </div>
          <div style={{ flex: 1 }}>
            <span style={{ fontWeight: 600 }}>Path Score: </span>
            <span
              style={{
                padding: '2px 8px',
                borderRadius: 4,
                background: path.path_score > 0.7 ? '#d4edda' : path.path_score > 0.5 ? '#fff3cd' : '#f8d7da',
                color: path.path_score > 0.7 ? '#155724' : path.path_score > 0.5 ? '#856404' : '#721c24',
                fontWeight: 600,
              }}
            >
              {path.path_score?.toFixed(4) ?? 'N/A'}
            </span>
          </div>
          <div style={{ fontSize: 12, color: '#6c757d' }}>
            {path.nodes.length} nodes, {path.edges.length} edges
          </div>
        </div>

        {/* Path visualization */}
        <div style={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap', gap: 6 }}>
          {path.nodes.map((node: PathNode, idx: number) => {
            const isLast = idx === path.nodes.length - 1;
            const edge = path.edges[idx];
            
            // Get node name from various sources
            const nodeName = node.nodeName || 
              nodeNameDict?.[node.nodeType]?.[node.nodeId] || 
              node.nodeId;
            
            return (
              <React.Fragment key={`node-${idx}`}>
                <div
                  style={{
                    padding: '6px 10px',
                    borderRadius: 6,
                    background: getNodeColor(node.nodeType),
                    color: 'white',
                    minWidth: 80,
                    maxWidth: 150,
                    textAlign: 'center',
                    boxShadow: '0 1px 3px rgba(0,0,0,0.15)',
                  }}
                  title={`${nodeName} (${node.nodeType})`}
                >
                  <div
                    style={{
                      fontWeight: 600,
                      fontSize: 12,
                      whiteSpace: 'nowrap',
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                    }}
                  >
                    {nodeName.length > 15 ? nodeName.substring(0, 15) + '...' : nodeName}
                  </div>
                  <div style={{ fontSize: 10, opacity: 0.85, textTransform: 'capitalize' }}>
                    {node.nodeType?.replace('_', ' ') || 'unknown'}
                  </div>
                </div>
                {!isLast && edge && (
                  <div style={{ display: 'flex', alignItems: 'center', gap: 4 }}>
                    <div
                      style={{
                        width: 30,
                        height: 2,
                        background: `linear-gradient(90deg, ${getNodeColor(path.nodes[idx].nodeType)}, ${getNodeColor(path.nodes[idx + 1]?.nodeType || 'unknown')})`,
                      }}
                    />
                    <div
                      style={{
                        padding: '2px 6px',
                        borderRadius: 4,
                        background: '#f8f9fa',
                        border: '1px solid #dee2e6',
                        fontSize: 10,
                        color: '#495057',
                        whiteSpace: 'nowrap',
                      }}
                      title={`${edge.edgeInfo}: ${edge.score?.toFixed(4)}`}
                    >
                      <div>{edge.edgeInfo?.replace('_', ' ')}</div>
                      <div style={{ fontWeight: 600, color: '#3a7bd5' }}>{edge.score?.toFixed(3)}</div>
                    </div>
                    <div
                      style={{
                        width: 30,
                        height: 2,
                        background: `linear-gradient(90deg, ${getNodeColor(path.nodes[idx].nodeType)}, ${getNodeColor(path.nodes[idx + 1]?.nodeType || 'unknown')})`,
                      }}
                    />
                  </div>
                )}
              </React.Fragment>
            );
          })}
        </div>
      </div>
    );
  }

  renderAllPaths() {
    const { pathData } = this.state;
    if (!pathData) {
      return (
        <div style={{ padding: 16, color: '#6c757d' }}>
          No path data available for this pair.
        </div>
      );
    }

    // Support both old format (path) and new format (paths)
    const paths = pathData.paths || (pathData.path ? [pathData.path] : []);

    if (paths.length === 0) {
      return (
        <div style={{ padding: 16, color: '#6c757d' }}>
          No paths found for this drug-disease pair.
        </div>
      );
    }

    return (
      <div style={{ padding: 16 }}>
        {/* Header */}
        <div
          style={{
            marginBottom: 16,
            background: '#e7f3ff',
            borderLeft: '4px solid #3a7bd5',
            padding: '12px 14px',
            borderRadius: 6,
          }}
        >
          <div style={{ fontWeight: 700, fontSize: 16, marginBottom: 4 }}>
            Top {paths.length} Most Important Path{paths.length > 1 ? 's' : ''}
          </div>
          <div style={{ color: '#495057' }}>
            <strong>Drug:</strong> {pathData.drug_id}  <strong>Disease:</strong>{' '}
            {pathData.disease_name} ({pathData.disease_node_id})
          </div>
          <div style={{ fontSize: 12, color: '#6c757d', marginTop: 4 }}>
            Paths ranked by average edge attention score (embedding similarity)
          </div>
        </div>

        {/* All paths */}
        {paths.map((path: PathData, idx: number) => this.renderSinglePath(path, idx + 1))}
      </div>
    );
  }

  render() {
    const { width, height } = this.props;
    const { loading, error } = this.state;
    const { selectedDrugPrimary, diseasePredictions } = this.props.globalState;
    const selectedDisease = diseasePredictions.find((d) => d.selected);

    if (!selectedDrugPrimary || !selectedDisease) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#6c757d',
          }}
        >
          Please select a drug and disease to see the top paths.
        </div>
      );
    }

    if (loading) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <svg width={100} height={100}>{LOADING_ICON}</svg>
        </div>
      );
    }

    if (error) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#dc3545',
          }}
        >
          {error}
        </div>
      );
    }

    return (
      <div style={{ width, height, overflow: 'auto' }}>
        {this.renderAllPaths()}
      </div>
    );
  }
}

