import * as React from 'react';
import { IState, IDispatch } from 'types';
import { LOADING_ICON, getNodeColor } from 'helpers';

interface Props {
  height: number;
  width: number;
  globalState: IState;
  dispatch: IDispatch;
}

interface PathNode {
  idx: number;
  id: string;
  name: string;
  type: string;
}

interface PathEdge {
  relation: string;
  attention: number;
}

interface PathData {
  hops: number;
  nodes: PathNode[];
  edges: PathEdge[];
  attention_scores: number[];
  avg_attention: number;
  min_attention: number;
  max_attention: number;
  combined_score: number;
  disease_name: string;
  disease_id: string;
}

interface AllPaths10HopData {
  drug: { id: string; name: string; type: string };
  max_hops: number;
  statistics: {
    total_paths: number;
    unique_diseases: number;
    mean_attention: number;
    std_attention: number;
    min_attention: number;
    max_attention: number;
    paths_by_hop: { [key: string]: number };
  };
  score_explanation: { [key: string]: string };
  global_top_paths: PathData[];
  paths_by_hops: { [key: string]: PathData[] };
  top_paths_per_disease: { [key: string]: PathData[] };
}

interface State {
  loading: boolean;
  error: string | null;
  data: AllPaths10HopData | null;
  minHops: number;
  maxHops: number;
  diseaseFilter: string;
  sortBy: 'combined_score' | 'avg_attention' | 'hops';
  showGuide: boolean;
  viewMode: 'global' | 'by_hop' | 'by_disease';
  selectedHop: number;
  selectedDisease: string;
}

export default class AllPaths10Hop extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      loading: false,
      error: null,
      data: null,
      minHops: 2,
      maxHops: 10,
      diseaseFilter: '',
      sortBy: 'combined_score',
      showGuide: false,
      viewMode: 'global',
      selectedHop: 4,
      selectedDisease: '',
    };
  }

  componentDidMount() {
    this.loadPaths();
  }

  componentDidUpdate(prevProps: Props) {
    const { selectedDrugPrimary } = this.props.globalState;
    if (selectedDrugPrimary !== prevProps.globalState.selectedDrugPrimary) {
      this.loadPaths();
    }
  }

  async loadPaths() {
    const { selectedDrugPrimary } = this.props.globalState;
    if (!selectedDrugPrimary) {
      this.setState({ data: null, loading: false });
      return;
    }

    this.setState({ loading: true, error: null });

    try {
      // Load from the 10-hop specific endpoint
      const url = `/api/all_paths_10hop?drug_id=${selectedDrugPrimary}`;
      const res = await fetch(url);
      if (!res.ok) throw new Error('Failed to fetch 10-hop paths');
      const data = await res.json();
      this.setState({ data, loading: false });
    } catch (error) {
      console.error('Failed to load 10-hop paths:', error);
      this.setState({ loading: false, error: 'Failed to load 10-hop paths. Make sure the data is generated.' });
    }
  }

  getFilteredPaths(): PathData[] {
    const { data, minHops, maxHops, diseaseFilter, sortBy, viewMode, selectedHop, selectedDisease } = this.state;
    if (!data) return [];

    let paths: PathData[] = [];

    // Select paths based on view mode
    if (viewMode === 'global') {
      paths = [...(data.global_top_paths || [])];
    } else if (viewMode === 'by_hop' && data.paths_by_hops) {
      paths = [...(data.paths_by_hops[selectedHop.toString()] || [])];
    } else if (viewMode === 'by_disease' && data.top_paths_per_disease && selectedDisease) {
      paths = [...(data.top_paths_per_disease[selectedDisease] || [])];
    } else {
      paths = [...(data.global_top_paths || [])];
    }

    // Filter by hop range
    paths = paths.filter((p) => p.hops >= minHops && p.hops <= maxHops);

    // Filter by disease name
    if (diseaseFilter.trim()) {
      const filter = diseaseFilter.toLowerCase();
      paths = paths.filter((p) => {
        const diseaseName = p.disease_name || p.nodes[p.nodes.length - 1]?.name || '';
        return diseaseName.toLowerCase().includes(filter);
      });
    }

    // Sort
    paths.sort((a, b) => {
      if (sortBy === 'combined_score') return (b.combined_score || 0) - (a.combined_score || 0);
      if (sortBy === 'avg_attention') return (b.avg_attention || 0) - (a.avg_attention || 0);
      return a.hops - b.hops;
    });

    return paths;
  }

  getAttentionColor(score: number): string {
    if (score >= 0.7) return '#28a745';
    if (score >= 0.6) return '#5cb85c';
    if (score >= 0.5) return '#f0ad4e';
    return '#d9534f';
  }

  renderPath(path: PathData, rank: number) {
    const { nodes, attention_scores, avg_attention, combined_score, hops } = path;

    return (
      <div
        key={`path-${rank}-${path.disease_name}`}
        style={{
          marginBottom: 12,
          padding: 12,
          border: '1px solid #e0e0e0',
          borderRadius: 8,
          background: rank <= 3 ? 'linear-gradient(135deg, #f8fff8 0%, #f0f8f0 100%)' : '#fff',
          boxShadow: rank === 1 ? '0 2px 8px rgba(40,167,69,0.15)' : 'none',
        }}
      >
        {/* Header */}
        <div style={{ display: 'flex', alignItems: 'center', marginBottom: 10, gap: 8, flexWrap: 'wrap' }}>
          <div
            style={{
              width: 26,
              height: 26,
              borderRadius: '50%',
              background: rank === 1 ? '#28a745' : rank <= 3 ? '#3a7bd5' : rank <= 10 ? '#6c757d' : '#adb5bd',
              color: 'white',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              fontWeight: 700,
              fontSize: 12,
              flexShrink: 0,
            }}
          >
            {rank}
          </div>

          <div style={{ display: 'flex', gap: 6, flexWrap: 'wrap', alignItems: 'center' }}>
            <span
              style={{
                padding: '2px 8px',
                borderRadius: 12,
                background: hops <= 4 ? '#e7f3ff' : hops <= 6 ? '#fff3cd' : hops <= 8 ? '#ffeaa7' : '#fdcb6e',
                color: hops <= 4 ? '#2b5797' : hops <= 6 ? '#856404' : '#5d4e37',
                fontSize: 10,
                fontWeight: 600,
              }}
            >
              {hops} hops
            </span>
            <span
              style={{
                padding: '2px 8px',
                borderRadius: 4,
                background: this.getAttentionColor(avg_attention),
                color: 'white',
                fontSize: 10,
                fontWeight: 600,
              }}
              title="Average embedding similarity"
            >
              Att: {avg_attention?.toFixed(3)}
            </span>
            <span
              style={{
                padding: '2px 8px',
                borderRadius: 4,
                background: '#495057',
                color: 'white',
                fontSize: 10,
                fontWeight: 600,
              }}
              title="Combined score = attention  (1/hops)"
            >
              Score: {combined_score?.toFixed(4)}
            </span>
          </div>

          <div
            style={{
              marginLeft: 'auto',
              fontSize: 11,
              fontWeight: 600,
              color: '#155724',
              background: '#d4edda',
              padding: '2px 8px',
              borderRadius: 4,
              maxWidth: 200,
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              whiteSpace: 'nowrap',
            }}
            title={path.disease_name}
          >
             {path.disease_name}
          </div>
        </div>

        {/* Path visualization */}
        <div style={{ display: 'flex', alignItems: 'center', flexWrap: 'wrap', gap: 3, overflowX: 'auto', paddingBottom: 4 }}>
          {nodes.map((node, idx) => {
            const isLast = idx === nodes.length - 1;
            const attention = attention_scores[idx];

            return (
              <React.Fragment key={`node-${idx}`}>
                <div
                  style={{
                    padding: '4px 6px',
                    borderRadius: 5,
                    background: getNodeColor(node.type),
                    color: 'white',
                    minWidth: 50,
                    maxWidth: 100,
                    textAlign: 'center',
                    boxShadow: '0 1px 2px rgba(0,0,0,0.1)',
                  }}
                  title={`${node.name} (${node.type})\nID: ${node.id}`}
                >
                  <div
                    style={{
                      fontWeight: 600,
                      fontSize: 10,
                      whiteSpace: 'nowrap',
                      overflow: 'hidden',
                      textOverflow: 'ellipsis',
                    }}
                  >
                    {node.name.length > 10 ? node.name.substring(0, 10) + '' : node.name}
                  </div>
                  <div style={{ fontSize: 8, opacity: 0.8 }}>
                    {node.type?.replace('_', ' ').replace('/', ' ')}
                  </div>
                </div>

                {!isLast && (
                  <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div
                      style={{
                        width: 12,
                        height: 2,
                        background: attention ? this.getAttentionColor(attention) : '#ccc',
                      }}
                    />
                    <div
                      style={{
                        padding: '1px 4px',
                        borderRadius: 3,
                        background: attention ? this.getAttentionColor(attention) : '#f0f0f0',
                        color: attention >= 0.5 ? 'white' : '#333',
                        fontSize: 8,
                        fontWeight: 600,
                      }}
                      title={`Attention: ${attention?.toFixed(4)}`}
                    >
                      {attention?.toFixed(2)}
                    </div>
                    <div
                      style={{
                        width: 12,
                        height: 2,
                        background: attention ? this.getAttentionColor(attention) : '#ccc',
                      }}
                    />
                  </div>
                )}
              </React.Fragment>
            );
          })}
        </div>
      </div>
    );
  }

  renderViewModeSelector() {
    const { viewMode, data, selectedHop, selectedDisease } = this.state;

    const diseases = data?.top_paths_per_disease ? Object.keys(data.top_paths_per_disease).slice(0, 100) : [];
    const hops = data?.paths_by_hops ? Object.keys(data.paths_by_hops).map(Number).sort((a, b) => a - b) : [];

    return (
      <div
        style={{
          display: 'flex',
          gap: 12,
          marginBottom: 16,
          padding: 12,
          background: '#e7f3ff',
          borderRadius: 8,
          flexWrap: 'wrap',
          alignItems: 'center',
        }}
      >
        <div style={{ fontWeight: 600, fontSize: 12 }}>View:</div>

        <button
          onClick={() => this.setState({ viewMode: 'global' })}
          style={{
            padding: '6px 12px',
            borderRadius: 4,
            border: viewMode === 'global' ? '2px solid #2b5797' : '1px solid #ccc',
            background: viewMode === 'global' ? '#2b5797' : '#fff',
            color: viewMode === 'global' ? '#fff' : '#333',
            cursor: 'pointer',
            fontWeight: 600,
            fontSize: 11,
          }}
        >
          Global Top Paths
        </button>

        <button
          onClick={() => this.setState({ viewMode: 'by_hop' })}
          style={{
            padding: '6px 12px',
            borderRadius: 4,
            border: viewMode === 'by_hop' ? '2px solid #2b5797' : '1px solid #ccc',
            background: viewMode === 'by_hop' ? '#2b5797' : '#fff',
            color: viewMode === 'by_hop' ? '#fff' : '#333',
            cursor: 'pointer',
            fontWeight: 600,
            fontSize: 11,
          }}
        >
          By Hop Count
        </button>

        <button
          onClick={() => this.setState({ viewMode: 'by_disease' })}
          style={{
            padding: '6px 12px',
            borderRadius: 4,
            border: viewMode === 'by_disease' ? '2px solid #2b5797' : '1px solid #ccc',
            background: viewMode === 'by_disease' ? '#2b5797' : '#fff',
            color: viewMode === 'by_disease' ? '#fff' : '#333',
            cursor: 'pointer',
            fontWeight: 600,
            fontSize: 11,
          }}
        >
          By Disease
        </button>

        {viewMode === 'by_hop' && hops.length > 0 && (
          <select
            value={selectedHop}
            onChange={(e) => this.setState({ selectedHop: parseInt(e.target.value) })}
            style={{ padding: '6px 8px', borderRadius: 4, border: '1px solid #ccc' }}
          >
            {hops.map((h) => (
              <option key={h} value={h}>
                {h}-hop ({data?.paths_by_hops?.[h.toString()]?.length || 0} paths)
              </option>
            ))}
          </select>
        )}

        {viewMode === 'by_disease' && diseases.length > 0 && (
          <select
            value={selectedDisease}
            onChange={(e) => this.setState({ selectedDisease: e.target.value })}
            style={{ padding: '6px 8px', borderRadius: 4, border: '1px solid #ccc', maxWidth: 200 }}
          >
            <option value="">Select a disease...</option>
            {diseases.map((d) => (
              <option key={d} value={d}>
                {d.length > 30 ? d.substring(0, 30) + '...' : d}
              </option>
            ))}
          </select>
        )}
      </div>
    );
  }

  renderFilters() {
    const { minHops, maxHops, diseaseFilter, sortBy } = this.state;

    return (
      <div
        style={{
          display: 'flex',
          gap: 12,
          marginBottom: 16,
          padding: 12,
          background: '#f8f9fa',
          borderRadius: 8,
          flexWrap: 'wrap',
          alignItems: 'center',
        }}
      >
        <div>
          <label style={{ fontSize: 11, fontWeight: 600, marginRight: 4 }}>Min:</label>
          <select
            value={minHops}
            onChange={(e) => this.setState({ minHops: parseInt(e.target.value) })}
            style={{ padding: '4px 6px', borderRadius: 4, border: '1px solid #ccc', fontSize: 11 }}
          >
            {[2, 3, 4, 5, 6, 7, 8, 9, 10].map((h) => (
              <option key={h} value={h}>
                {h}
              </option>
            ))}
          </select>
        </div>

        <div>
          <label style={{ fontSize: 11, fontWeight: 600, marginRight: 4 }}>Max:</label>
          <select
            value={maxHops}
            onChange={(e) => this.setState({ maxHops: parseInt(e.target.value) })}
            style={{ padding: '4px 6px', borderRadius: 4, border: '1px solid #ccc', fontSize: 11 }}
          >
            {[2, 3, 4, 5, 6, 7, 8, 9, 10].map((h) => (
              <option key={h} value={h}>
                {h}
              </option>
            ))}
          </select>
        </div>

        <div>
          <label style={{ fontSize: 11, fontWeight: 600, marginRight: 4 }}>Sort:</label>
          <select
            value={sortBy}
            onChange={(e) => this.setState({ sortBy: e.target.value as any })}
            style={{ padding: '4px 6px', borderRadius: 4, border: '1px solid #ccc', fontSize: 11 }}
          >
            <option value="combined_score">Combined Score</option>
            <option value="avg_attention">Attention</option>
            <option value="hops">Path Length</option>
          </select>
        </div>

        <div style={{ flex: 1, minWidth: 150 }}>
          <input
            type="text"
            placeholder="Filter by disease..."
            value={diseaseFilter}
            onChange={(e) => this.setState({ diseaseFilter: e.target.value })}
            style={{
              width: '100%',
              padding: '5px 8px',
              borderRadius: 4,
              border: '1px solid #ccc',
              fontSize: 11,
            }}
          />
        </div>
      </div>
    );
  }

  renderStatistics() {
    const { data } = this.state;
    if (!data?.statistics) return null;

    const { total_paths, unique_diseases, mean_attention, paths_by_hop } = data.statistics;

    return (
      <div style={{ marginBottom: 16 }}>
        {/* Main stats */}
        <div style={{ display: 'flex', gap: 12, marginBottom: 12, flexWrap: 'wrap' }}>
          <div
            style={{
              flex: 1,
              minWidth: 100,
              padding: 10,
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              borderRadius: 8,
              textAlign: 'center',
              color: 'white',
            }}
          >
            <div style={{ fontSize: 18, fontWeight: 700 }}>{total_paths?.toLocaleString()}</div>
            <div style={{ fontSize: 10 }}>Total Paths</div>
          </div>
          <div
            style={{
              flex: 1,
              minWidth: 100,
              padding: 10,
              background: 'linear-gradient(135deg, #11998e 0%, #38ef7d 100%)',
              borderRadius: 8,
              textAlign: 'center',
              color: 'white',
            }}
          >
            <div style={{ fontSize: 18, fontWeight: 700 }}>{unique_diseases}</div>
            <div style={{ fontSize: 10 }}>Diseases</div>
          </div>
          <div
            style={{
              flex: 1,
              minWidth: 100,
              padding: 10,
              background: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
              borderRadius: 8,
              textAlign: 'center',
              color: 'white',
            }}
          >
            <div style={{ fontSize: 18, fontWeight: 700 }}>{mean_attention?.toFixed(3)}</div>
            <div style={{ fontSize: 10 }}>Avg Attention</div>
          </div>
          <div
            style={{
              flex: 1,
              minWidth: 100,
              padding: 10,
              background: 'linear-gradient(135deg, #4facfe 0%, #00f2fe 100%)',
              borderRadius: 8,
              textAlign: 'center',
              color: 'white',
            }}
          >
            <div style={{ fontSize: 18, fontWeight: 700 }}>{data.max_hops}</div>
            <div style={{ fontSize: 10 }}>Max Hops</div>
          </div>
        </div>

        {/* Hop distribution bar chart */}
        {paths_by_hop && (
          <div style={{ background: '#f8f9fa', padding: 12, borderRadius: 8 }}>
            <div style={{ fontSize: 11, fontWeight: 600, marginBottom: 8 }}>Paths by Hop Count:</div>
            <div style={{ display: 'flex', gap: 6, alignItems: 'flex-end', height: 50 }}>
              {Object.entries(paths_by_hop)
                .sort((a, b) => parseInt(a[0]) - parseInt(b[0]))
                .map(([hop, count]) => {
                  const maxCount = Math.max(...Object.values(paths_by_hop));
                  const height = Math.max(8, ((count as number) / maxCount) * 40);
                  const hopNum = parseInt(hop);
                  const hue = 210 - (hopNum - 2) * 15; // Color gradient
                  return (
                    <div key={hop} style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', flex: 1 }}>
                      <div
                        style={{
                          width: '100%',
                          maxWidth: 30,
                          height,
                          background: `hsl(${hue}, 65%, 50%)`,
                          borderRadius: '3px 3px 0 0',
                        }}
                        title={`${count} paths`}
                      />
                      <div style={{ fontSize: 9, color: '#6c757d', marginTop: 2 }}>{hop}</div>
                      <div style={{ fontSize: 8, fontWeight: 600 }}>{count}</div>
                    </div>
                  );
                })}
            </div>
          </div>
        )}
      </div>
    );
  }

  renderInterpretationGuide() {
    const { showGuide } = this.state;

    return (
      <div
        style={{
          marginBottom: 16,
          border: '1px solid #dee2e6',
          borderRadius: 8,
          overflow: 'hidden',
        }}
      >
        <div
          onClick={() => this.setState({ showGuide: !showGuide })}
          style={{
            padding: '10px 14px',
            background: showGuide ? '#2b5797' : '#f8f9fa',
            color: showGuide ? 'white' : '#495057',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between',
            fontWeight: 600,
            fontSize: 12,
          }}
        >
          <span> 10-Hop Path Interpretation Guide</span>
          <span>{showGuide ? '' : ''}</span>
        </div>

        {showGuide && (
          <div style={{ padding: 14, background: '#fff', fontSize: 12, lineHeight: 1.6 }}>
            <div style={{ marginBottom: 16 }}>
              <h4 style={{ margin: '0 0 8px 0', color: '#2b5797', fontSize: 13 }}>Why 10-Hop Paths?</h4>
              <p style={{ margin: 0, color: '#495057' }}>
                Longer paths (6-10 hops) reveal <strong>indirect biological mechanisms</strong> that connect drugs to diseases
                through complex networks of proteins, pathways, and biological processes. While shorter paths show direct
                connections, longer paths can uncover novel repurposing opportunities.
              </p>
            </div>

            <div style={{ marginBottom: 16 }}>
              <h4 style={{ margin: '0 0 8px 0', color: '#2b5797', fontSize: 13 }}>Path Types by Length</h4>
              <div style={{ display: 'grid', gap: 8 }}>
                <div style={{ background: '#e7f3ff', padding: 8, borderRadius: 4, borderLeft: '3px solid #2b5797' }}>
                  <strong>2-4 hops:</strong> Direct mechanisms (target-based, drug similarity)
                </div>
                <div style={{ background: '#d4edda', padding: 8, borderRadius: 4, borderLeft: '3px solid #28a745' }}>
                  <strong>5-6 hops:</strong> Pathway-mediated effects (gene networks, biological processes)
                </div>
                <div style={{ background: '#fff3cd', padding: 8, borderRadius: 4, borderLeft: '3px solid #ffc107' }}>
                  <strong>7-8 hops:</strong> Complex mechanisms (multi-pathway, tissue-specific)
                </div>
                <div style={{ background: '#f8d7da', padding: 8, borderRadius: 4, borderLeft: '3px solid #dc3545' }}>
                  <strong>9-10 hops:</strong> Exploratory connections (potential novel mechanisms)
                </div>
              </div>
            </div>

            <div style={{ background: '#f8f9fa', padding: 10, borderRadius: 4, borderLeft: '3px solid #17a2b8' }}>
              <strong> Key Insight:</strong> Higher attention scores on longer paths indicate that TxGNN found meaningful
              semantic similarity between distant nodes, suggesting biologically relevant connections worth investigating.
            </div>
          </div>
        )}
      </div>
    );
  }

  render() {
    const { width, height } = this.props;
    const { loading, error, data } = this.state;
    const { selectedDrugPrimary } = this.props.globalState;

    if (!selectedDrugPrimary) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#6c757d',
          }}
        >
          Please select a drug to explore 10-hop paths.
        </div>
      );
    }

    if (loading) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
          }}
        >
          <svg width={100} height={100}>
            {LOADING_ICON}
          </svg>
        </div>
      );
    }

    if (error) {
      return (
        <div
          style={{
            width,
            height,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            color: '#dc3545',
            gap: 12,
          }}
        >
          <div>{error}</div>
          <div style={{ fontSize: 12, color: '#6c757d' }}>
            Run: <code>python generate_deep_paths_10hop.py</code> to generate 10-hop paths for Inebilizumab.
          </div>
        </div>
      );
    }

    const filteredPaths = this.getFilteredPaths();

    return (
      <div style={{ width, height, overflow: 'auto', padding: 12 }}>
        {/* Header */}
        <div
          style={{
            marginBottom: 14,
            background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
            padding: '12px 16px',
            borderRadius: 8,
            color: 'white',
          }}
        >
          <div style={{ fontWeight: 700, fontSize: 16, marginBottom: 4 }}> 10-Hop Deep Path Explorer</div>
          <div style={{ fontSize: 12, opacity: 0.9 }}>
            Comprehensive paths (2-10 hops) from <strong>{data?.drug?.name || selectedDrugPrimary}</strong> to diseases
          </div>
        </div>

        {/* Guide */}
        {this.renderInterpretationGuide()}

        {/* Statistics */}
        {this.renderStatistics()}

        {/* View Mode Selector */}
        {this.renderViewModeSelector()}

        {/* Filters */}
        {this.renderFilters()}

        {/* Results count */}
        <div style={{ marginBottom: 10, fontSize: 12, color: '#495057' }}>
          Showing <strong>{Math.min(filteredPaths.length, 50)}</strong> of {filteredPaths.length} paths
        </div>

        {/* Paths */}
        {filteredPaths.length === 0 ? (
          <div style={{ textAlign: 'center', color: '#6c757d', padding: 24 }}>No paths match your filters.</div>
        ) : (
          filteredPaths.slice(0, 50).map((path, idx) => this.renderPath(path, idx + 1))
        )}

        {filteredPaths.length > 50 && (
          <div style={{ textAlign: 'center', color: '#6c757d', padding: 12, fontSize: 11 }}>
            Showing top 50 paths. Use filters to see more specific results.
          </div>
        )}
      </div>
    );
  }
}

